.header {
    min-height: 400px;
    height: 100vh;
    margin: 0 auto;
    min-width: 414px;
    max-width: 1414px;
    background-image: url(../images/header_photo.jpg);
    background-position: center;
    background-size: cover;
    &_wrapper {
        transform: translate(-50%, -50%);
        width: 300px;
        height: 300px;
        border-radius: 50%;
        top: 40%;
        left: 50%;
        @media (max-height: 500px) {
            top: 40%;
            width: 200px;
            height: 200px;
        }
    }
    &_tittle {
        position: absolute;
        width: 90%;
        color: white;
        top: 80%;
        left: 50%;
        transform: translate(-50%, -50%);
        @media (max-height: 500px) {
            font-size: 24px;
        }
    }
}