.contact {
  min-width: 414px;
  max-width: 1414px;
  background-color: rgba(0, 0, 0, 0.01);
}
.contact_container {
  margin: 10px auto 0 auto;
  max-width: 800px;
}
.contact_form {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 350px;
  height: 400px;
  padding: 2%;
}
.contact_label {
  padding-bottom: 2%;
  display: flex;
  justify-content: space-between;
}
.contact_name {
  width: 30%;
  font-weight: 700;
}
.contact_input {
  width: 70%;
  padding: 1%;
  border-radius: 10px;
}
.contact_message {
  height: 150px;
  border-radius: 10px;
  padding: 2%;
  outline: none;
}
.contact_button {
  width: 100px;
  text-transform: uppercase;
  padding: 5px;
  background-color: black;
  border-radius: 20px;
  color: white;
  margin: 10px auto;
}
.contact_button:hover {
  cursor: pointer;
}
.contact_map {
  display: block;
  margin: 20px auto;
  border: none;
  width: 350px;
  height: 400px;
}/*# sourceMappingURL=contact.css.map */